import styled from "styled-components";

import { colors, spacingMap } from "../styleguide";
import Typography from "../Typography/Typography";
import { pxToRem } from "../utils";

import { StyledSidebarItemProps } from "./SidebarItem.types";

export const StyledSidebarItem = styled(Typography)<StyledSidebarItemProps>`
	display: flex;
	box-sizing: border-box;
	margin: 0;
	opacity: 0.66;
	height: 36px;
	align-items: flex-start;
	padding: ${spacingMap.xs};
	&:hover{
		opacity: 1;
	}
	border: solid 1px transparent;
	border-radius: 8px;
	gap: ${spacingMap.xs};

	${({ isActive }) => isActive && `
		opacity: 1;
		color: ${colors.black};
		background-color: ${colors.lightGray}80;
		border: solid 1px ${colors.white};
		font-weight: bold;
	`}

	${({ isParent }) => isParent && `
		border-bottom: 0;
		border-radius: 8px 8px 0 0;
	`}

	${({ isDisabled }) => isDisabled && `
		color: ${colors.black};
		cursor: not-allowed;
		opacity: .33;
		&:hover{
			opacity: .33;
		};
	`}
`;

export const RightBar = styled.span<StyledSidebarItemProps>`
	position: absolute;
	width: 4px;
	height: 35px;
	border-radius: 2px 0 0 2px;
	right: -8px;
	top: ${pxToRem(1)};
	${({ isActive }) => isActive && `
		opacity: 1;
		background-color: ${colors.black};
		color: ${colors.black};
	`}
	&:focus, &:focus-visible {
		outline: none;
	}
`;
